class CustomForm extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      email: ""
    };
  }

  handleSubmit = event => {
    const { onValidated } = this.props;
    console.log(this.email);
  };

  handleChange = event => {
    this.setState({ email: event.target.value });
  };

  render() {
    const { status, message, onValidated } = this.props;

    let email;
    const submitting = status === "sending";

    return (
      <Form>
        <FormGroup>
          <Label className="sr-only">Email Address</Label>
          <Input
            value={this.state.email}
            onChange={this.handleChange}
            type="email"
            placeholder="joe@example.com"
          />
          <FormText color="muted">
            We won't sell your email. We hate that.
          </FormText>

          {status === "sending" && (
            <div style={{ color: "blue" }}>sending...</div>
          )}
          {status === "error" && (
            <div
              style={{ color: "red" }}
              dangerouslySetInnerHTML={{ __html: message }}
            />
          )}
          {status === "success" && (
            <div
              style={{ color: "green" }}
              dangerouslySetInnerHTML={{ __html: message }}
            />
          )}
        </FormGroup>
        <Button
          disabled={submitting}
          block
          color="primary"
          onClick={this.handleSubmit}
        >
          Subscribe
        </Button>
      </Form>
    );
  }
}

export const MailChimpForm = props => {
  const url =
    "//twitter.us14.list-manage.com/subscribe/post?u=c995eaf6129ecf4b13d8ae469&id=dd2d98db0f";
  return (
    <div>
      <MailchimpSubscribe
        url={url}
        render={({ subscribe, status, message }) => (
          <CustomForm
            status={status}
            message={message}
            onValidated={formData => subscribe(formData)}
          />
        )}
      />
    </div>
  );
};
